@import url(https://fonts.googleapis.com/css2?family=Alegreya+Sans:wght@300&family=Poiret+One&family=Taviraj:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
    background-color: rgb(157, 212, 238);
    height: 60px;
    display: flex;
    justify-content: start;
    align-items: center;
    position: fixed;
}

.menu-bars {
    margin-left: 1.5rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    background-color: rgb(40, 45, 49);
    width: 220px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -90%;
    transition: 900ms;
}

.nav-menu.active {
    left: 0;
    transition: 400ms;
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 8px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: #fff;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: rgb(243, 221, 217);
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: rgb(40, 45, 49);
    height: 60px;
    justify-content: flex-end;
    padding-right: 10px;
    display: flex;
    align-items: center;
}

span {
    margin-left: 8px;
}
.profile-page {
    background-color: rgb(157, 212, 238);
    padding-bottom: 10px;
}

.profile-image {
    height: 250px;
    width: 250px;
}

.container-header.main {
    background-color: #fff;
    margin: auto;
    align-items: center;
    justify-content: center;
    display: flex;
    /* padding-bottom: 50px; */
    width: 900px;
    border-radius: 10px;
    float: none;
    display: block;
}

.main-name.center.aligned.header {
    padding-top: 70px;
    color: rgb(22, 29, 49);
    font-size: 4em;
    font-weight: 200;
    font-family: 'Alegreya Sans', sans-serif;
    margin: auto;
    justify-content: center;
    align-items: center;
    padding-bottom: 10px;
}

.resume {
    padding-bottom: 10px;
    align-self: center;
    justify-content: center;
    margin: 0 auto 0 auto;
    width: 200px;
    z-index: 2;
    height: 75%;
    display: block;
    margin: 0 auto 0 auto;

}

.about {
    width: 700px;
    justify-content: center;
    padding-top: 20px;
    margin: auto;
    padding-bottom: 60px;
    -webkit-margin-after: auto;
            margin-block-end: auto;
    color: rgb(43, 47, 49);
}



.technologies {
    width: 100%;
    margin-top: 60px;
    margin-bottom: 50px;
    padding-bottom: 40px;
}

.tech-title {
    color: rgb(42, 41, 44);
    font-size: 20px;
    font-weight: 100;
    text-align: center;
    padding-top: 10px;
}

.tech-list {
    padding: 10px;
    margin: auto;
    display: flex;
    background-color: #fff;
    justify-content: space-between;
    font-weight: 400px;
}

.html-image {
    height: 80px;
    width: 150px;
}

.node-image {
    height: 80px;
    width: 87px;
}

.react-image {
    height: 80px;
    width: 87px;
}

.tech-images {
    height: 80px;
    width: 80px;
}
.wrapper-projects {
    width: 900px;
    margin: 0 auto 0 auto;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    padding: 20px;
}
.container-projects {
    margin: 0 auto 0 auto;
    align-items: center;
    justify-content: center;
    display: flex;
    padding-bottom: 60px;
    width: 100% !important;
    background-color: rgb(157, 212, 238);
}

.Item.Image {
    padding-left: 20px;
}

.projects-list {
    font-size: 14px;
}

.title.center.aligned.header {
    color: rgb(46, 43, 42);
    font-size: 3em;
    font-weight: 600;
    padding-top: 30px;
    margin: auto;
    justify-content: center;
    align-items: center;
    padding-bottom: 30px;
}

.contact-page {
    height: 500px;
    width: 700px;
    justify-content: center;
    margin: auto;
}

.contact-text {
    justify-content: center;
    margin: auto;
    display: flex;
    color: rgb(59, 58, 59);
    padding-bottom: 15px;
}

.contact-icons {
    justify-content: center;
    margin: auto;
    display: flex;
}

.icons-contact {
    width: 35px;
    height: 35px;
}

.heading {
    color: rgb(230, 173, 173);
    font-size: 82px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 200;
}
.footer {
    min-height: 150px;
    background: #10101f;
    color: #fff;
    position: relative;
    clear: both;
    padding: 20px;
    font-size: 2vh;
    justify-content: center;
    width: 100%;
    bottom: 0;
    display: flex;
  }
  
 
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body::-webkit-scrollbar, .imgrev::before {
  background: #fff;
}

body::-webkit-scrollbar-thumb {
  background: rgb(157, 212, 238);
  scroll-behavior: smooth;
}

.home, 
.projects, 
.contact {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

